import React, { useRef } from "react";
import { useGLTF } from "@react-three/drei";

export function Neighbor(props) {
  const { nodes, materials } = useGLTF("/glossarySelected/neighbor.glb");
  return (
    <group {...props} dispose={null}>
      <group //position={[-3.27, -0.66, 2.1]}
      >
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere155.geometry}
          material={materials["glass.004"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere155_1.geometry}
          material={materials["glass.001"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere155_2.geometry}
          material={materials.outline}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere155_3.geometry}
          material={materials["glass.005"]}
        />
      </group>
    </group>
  );
}
export default Neighbor;
useGLTF.preload("/glossarySelected/neighbor.glb");
